.banner {
  color: rgb(255, 255, 255);
  object-fit: contain;
  height: 580px;
  /* overflow-y: hidden; */
  /* color: black; */
  /* margin-top: 80px; */
}

.banner_contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 290px;
}
.banner_title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner_description {
  width: 45rem;
  line-height: 1.3rem;
  padding-top: 1rem;
  margin-left: 3rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
  /* background-color: rgba(51, 51, 51, 0.3); */
}
.banner_buttons {
  margin-left: 3rem;
}
.banner_button {
  cursor: pointer;
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
}
.banner_button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}

.banner--fadeBottom{
   height: 4.37rem;
   background-image:linear-gradient(180deg,transparent, rgb(37,37,1), #111);  
  /* background-image: linear-gradient(180deg, transparent, rgb(249, 249, 96), rgb(79, 250, 250)); */
   margin-top: 6.28rem;
   margin-bottom: 0rem;
   padding-bottom: 0rem ;
}


@media screen and (max-width: 480px) {
   .banner{
     height: 380px;
   }
  .banner_title {
  font-size: 2rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
 }
 .banner_description {
  width: 45rem;
  line-height: 1.1rem;
  padding-top: 1rem;
  margin-left: .2rem;
  font-size: 0.7rem;
  max-width: 100%;
  height: 80px;
  /* background-color: rgba(51, 51, 51, 0.3); */
 }
}
